<!--个人中心信息 -->
<template>
  <div class="personinfo" style="margin:20px 10px;">
    <el-row>
      <el-col style="width: 270px;">
        <img src="@/assets/img/personinfo/person.png" style="width: 270px;">
        <div class="myfullname">{{fullname}}</div>
        <el-menu :default-active="activeindex" class="el-menu-vertical-demo personmenu" @select="handleSelect" active-text-color="#409eff" :style="currentheight">
          <el-menu-item index="1" style="text-align: center;">
            <span slot="title" class="persontitle">课程中心</span>
          </el-menu-item>
          <el-menu-item index="2" style="text-align: center;">
            <span slot="title" class="persontitle">个人中心</span>
          </el-menu-item>
        </el-menu>
      </el-col>
      <el-col style="width: calc(100% - 488px);margin: 0px 94px;">
        <div v-if="activeindex==='1'">
          <div class="myinfotitle">我的课程</div>
          <el-menu :default-active="activecoursetype" class="el-menu-vertical-demo personmenu" mode="horizontal" @select="handleSelectCourseType" active-text-color="#ffd04b" style="margin-top: -20px;">
            <el-menu-item index="1" style="text-align: center;">
              <span slot="title" class="persontitle">付费课程</span>
            </el-menu-item>
            <el-menu-item index="2" style="text-align: center;">
              <span slot="title" class="persontitle">免费课程</span>
            </el-menu-item>
            <el-menu-item index="3" style="text-align: center;">
              <span slot="title" class="persontitle">全部课程</span>
            </el-menu-item>
          </el-menu>
          <button class="scanmore" @click="scanmore">查看更多</button>
          <div v-if="activecoursetype==='1'">
            <img @click="$router.push({path: '/courseplay' })" class="mycourseimg" src="@/assets/img/personinfo/course.png" style="margin-right:114px;">
            <img @click="$router.push({path: '/courseplay' })" class="mycourseimg" src="@/assets/img/personinfo/course.png">
          </div>
          <div v-else>
            <img @click="$router.push({path: '/courseplay' })" class="mycourseimg" src="@/assets/img/freecourse/customized.png" style="margin-right:114px;">
            <img @click="$router.push({path: '/courseplay' })" class="mycourseimg" src="@/assets/img/freecourse/customized.png">
          </div>
        </div>
        <div v-if="activeindex==='2'">
          <div class="myinfotitle">个人中心</div>
          <div class="myinfocontent">昵称：{{fullname}}</div>
          <div class="myinfocontent">性别：O 男同学 O女同学</div>
          <div class="myinfocontent">出生年月： 1993 - 11 - 02</div>
          <div class="myinfocontent">所读年级：高三</div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  name: 'person-view',
  data() {
    return {
      fullname: '',
      activeindex: '2', // 1=我的课程，2=个人中心
      activecoursetype: '1', // 课程类型，1=收费，2=免费，3=全部
      currentheight: '',
    }
  },
  created() {
    this.$emit('header', true);
    this.$emit('footer', false);
    this.currentheight = `height:${window.innerHeight - 343}px;border-right: solid 1px #CCCCCC;margin-top: -17px;`;
    const userinfo = localStorage.getItem('usg')
    if (userinfo !== 'underfined' && userinfo !== null) {
      this.fullname = JSON.parse(userinfo).fullname;
    }
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
      this.activeindex = key;
    },
    handleSelectCourseType(key, keyPath) {
      console.log(key, keyPath);
      this.activecoursetype = key;
    },
    scanmore() {
      let url = '';
      if (this.activecoursetype === '1') {
        url = '/customized';
      } else {
        url = '/freecourse';
      }
      const routeData = this.$router.resolve({
        name: '',
        path: url,
        query: '',
        params: {},
      });
      window.open(routeData.href, '_blank');
    },
  },
}
</script>
<style scoped>
  .persontitle{
    width: 121px;
    height: 53px;
    line-height: 44px;
    font-size: 30px;
    font-family: Microsoft Yahei;
  }
  .myfullname{
    text-align: center;
    font-size: 20px;
    color: #4C535D;
    position: relative;
    top: -55px;
    background: #fff;
  }
  .personmenu>>>.el-menu-item, .el-submenu__title {
    height: 94px;
    line-height: 94px;
    position: relative;
    box-sizing: border-box;
    white-space: nowrap;
    list-style: none;
  }
  .myinfotitle{
    margin-bottom: 70px;
    width: 145px;
    height: 53px;
    line-height: 52px;
    color: rgba(16, 16, 16, 1);
    font-size: 36px;
    font-weight: bold;
  }
  .myinfocontent{
    margin: 15px 0px;
    height: 53px;
    line-height: 44px;
    color: rgba(16, 16, 16, 1);
    font-size: 30px;
  }
  .scanmore{
    display: block;
    margin: 31px 0px 43px auto;
    width: 158px;
    height: 43px;
    line-height: 41px;
    border-radius: 10px;
    background-color: rgba(255, 152, 0, 1);
    color: rgba(255, 255, 255, 1);
    font-size: 30px;
    text-align: center;
    font-family: Roboto;
    border: 1px solid rgba(187, 187, 187, 1);
  }
  .mycourseimg{
    height: 349px;
  }
</style>



